@import '../../default.scss';

.info {
    padding-top: 15%;
    display: flex;
    align-items: center;
    justify-content: center;

    &__avatar {
        filter: drop-shadow(0px 0px 15px rgba(0, 0, 0, 0.5));
        width: 20%;
        height: 20%;
    }

    &__main {
        margin-left: 25px;
        text-align: left;

        &__name {
            color: var(--font_main);
            font-weight: 500;
            font-size: calc(20px + 32 * (100vw / 1440));
        }
        &__spec {
            color: var(--font_second);
            font-weight: 500;
            font-size: calc(8px + 18 * (100vw / 1440));
        }
        &__links {
            display: flex;
            margin-top: 15px;
        }
    }
}

.descr {
    color: var(--font_second);
    font-weight: 500;
    font-size: calc(8px + 18 * (100vw / 1440));
    margin: 0% 25% 0% 25%;
}

.infoMore {
    display: flex;
    padding-top: 40px;
    margin: 0% 25% 0% 25%;
    justify-content: space-between;
}

.btns {
    display: flex;
    justify-content: center;
    margin: 5% 5% 10% 5%;
}

@media (max-width: 768px) {
    .info {
        flex-direction: column;
    
        &__avatar {
            margin-bottom: 5%;
            width: 40%;
            height: 40%;
        }

        &__main {
            margin-left: 0;
            text-align: center;
    
            &__links {
                padding: 5% 20%;
                justify-content: space-between;
            }
        }
    }
    .descr {
        margin: 0% 5vw 0% 5vw;
    }
    
    .infoMore {
        padding-top: 20px;
        margin: 0% 5vw 0% 5vw;
    }
}

@media (max-width: 700px) {
    .info {
        padding-top: 35%;
    }
}