@import '../../default.scss';

:root[data-theme="light"] {
    --back: #E9EBEC;
    --font_main: #3d3d3d;
    --font_second: #575757;
    --accent_color: #FBD144;
    --accent_color2: #FFE071;
    --base_color: #fff;
    --second_base: #D7D7D7;
  }
  
:root[data-theme="dark"] {
    --back: #0C151D;
    --font_main: #F1F2F4;
    --font_second: #A3ABB2;
    --accent_color: #FFE071;
    --accent_color2: #FBD144;
    --base_color: #171F26;
    --second_base: #091016;
}

a:not(:last-child) {
    margin-right: 4%;
}

.btn {
    width: 290px;
    height: 80px;
    font-size: calc(16px + 8 * (100vw / 1440));
    border: none;
    border-radius: 10px;

    &:hover {
        transition: 1s;
        cursor: pointer;
    }   
}

@media (max-width: 768px) {
    .btn {
        width: 40vw;
        height: 12vw;
        font-size: calc(8px + 10 * (100vw / 768));
    }
}

.btnDonate {
    background: var(--accent_color);
    color: var(--font_main1);

    &:hover {
        background: var(--accent_color2);
        color: var(--font_second1);
    }
}

.btnOther {
    background: var(--base_color);
    color: var(--font_second);

    &:hover {
        background: var(--second_base);
    }
}