@import '../../default.scss';

:root[data-theme="light"] {
    --font_main1: #3d3d3d;
    --font_second1: #575757;
    --accent_color: #FBD144;
}
  
:root[data-theme="dark"] {
    --font_main1: #3d3d3d;
    --font_second1: #575757;
    --accent_color: #FFE071;
}

.portfolioItem {
    width: 28vw;
    height: 19vw;
    border-radius: 20px;
    margin-bottom: 7%;
    
    &__img {
        width: 100%;
        height: 100%;
        opacity: 1;
        border-radius: 20px;
    }
    &__info {
        width: 100%;
        height: 100%;
        opacity: 1;
        background: var(--accent_color);
        border-radius: 20px;
        text-align: center;

        &__header {
            padding-top: 15%;
            font-size: calc(14px + 14 * (100vw / 1440));
            letter-spacing: -0.53px;
            font-weight: 500;
            color: var(--font_main1);
        }
        &__descr {
            font-size: calc(10px + 8 * (100vw / 1440));
            margin: 0 10%;
            letter-spacing: -0.53px;
            font-weight: 500;
            color: var(--font_second1);
        }
        &__btn {
            border: none;
            margin-top: 5%;
            padding: 10px;
            width: 38px;
            height: 38px;
            border-radius: 25px;
            background: #fff;

            &:hover {
                cursor: pointer;
                transition: 0.3s;
                border-radius: 15px;
            }
        }
    }
}

@media (max-width: 768px) {
    .portfolioItem {
        width: 100vw;
        height: 60vw;
    }
}