
:root[data-theme="light"] {
    --back1: #fff;
    --back_image: url('../../img/toggle.svg');
}
  
:root[data-theme="dark"] {
    --back1: #171F26;
    --back_image: url('../../img/toggle2.svg');
}

.toggle {
    position: absolute;
    top: 10%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80px;
    height: 80px;
    padding: 10px;
    background: var(--back1);
    background-size: 50%;
    background-image: var(--back_image);
    background-repeat: no-repeat;
    background-position: center;
    border: none;
    border-radius: 50px;

    &:hover {
        transition: 0.5s;
        cursor: pointer;
        opacity: 0.8;
    }
}

@media (max-width: 768px) {
    .toggle {
        width: 35px;
        height: 35px;
    }
}