@import '../../default.scss';

.infoAdd {
    text-align: center;
    width: 25%;

    &__value {
        font-size: calc(10px + 18 * (100vw / 1440));
        color: var(--font_main);
    }
    &__text {
        font-size: calc(8px + 18 * (100vw / 1440));
        color: var(--font_second);
    }
}