@import '../../default.scss';

:root[data-theme="light"] {
    --font_main1: #3d3d3d;
    --accent_color: #FBD144;
}
  
:root[data-theme="dark"] {
    --font_main1: #3d3d3d;
    --accent_color: #FFE071;
}

.skillsItem {
    width: 250px;
    height: 100px;
    border-radius: 1.5vw;
    margin-bottom: 7%;

    &__img {
        padding: 10% 40%;
        width: 100%;
        height: 100%;
        background: #fff;
        opacity: 1;
        border-radius: 1.5vw;

        img {
            width: 50px;
            height: 50px;
        }
    }
    &__info {
        width: 100%;
        height: 100%;
        opacity: 1;
        background: var(--accent_color);
        border-radius: 1.5vw;
        text-align: center;

        &__header {
            padding-top: 12%;
            font-size: calc(14px + 14 * (100vw / 1440));
            letter-spacing: -0.53px;
            font-weight: 500;
            color: var(--font_main1);
        }
    }
}

    @media (max-width: 768px) {
        .skillsItem {
            width: 150px;
            height: 60px;

            &__img {
                img {
                    width: 30px;
                    height: 30px;
                }
            }
        }
    }