@import '../../default.scss';

.tabMenu {
    margin: 6% 18%;
    padding: 1% 1.5%;
    display: flex;
    justify-content: space-between;
    background: var(--base_color);
    border-radius: 20px;
}

@media (max-width: 768px) {
    .tabMenu {
        border-radius: 15px;
        padding: 1.5% 2%;
        margin: 10% 5%;    
    }
}

.tabBtn {
    width: 380px;
    height: 100px;
    font-size: calc(8px + 18 * (100vw / 1440));
    color: var(--font_second);
    background: var(--base_color);
    border: none;
    border-radius: 10px;

    &:hover {
        transition: 0.5s;
        background: var(--second_base);
        cursor: pointer;
    }
}

@media (max-width: 768px) {
    .tabBtn {
        width: 44vw;
        height: 12vw;
        font-size: calc(6px + 10 * (100vw / 768));
    }
}

.tabBtnPress {
    background: var(--second_base);
}

.slider {
    margin: 0 18%;
    height: auto;
    border-radius: 15px;

    &__projects {
        width: 100%;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    &__skills {
        width: 100%;
        flex-wrap: wrap;
        justify-content: space-between;
    }
}

@media (max-width: 768px) {
    .slider {
        margin: 0 5%;
    }
}

